@import "../../style.scss";

.page {
  width: 80%;
  margin-bottom: 10px;
}
.phoneinput_select {
  width: 70px;
  height: 50px;
}
.tel-box {
  display: flex;
  align-items: center;
}
.tel {
  height: 34px;
  border: 1px solid $darkGrey;
  outline: $textColor;
  border-radius: 111px;
  padding: 0 30px;
  height: 70px;
  width: 100%;
  font-size: 20px;
  text-align: center;
}
.select-box {
  position: relative;
}
.default_value {
  margin: 0 10px;
  font-size: 20px;
}
select {
  background: transparent;
  outline: none;
  border: none;
  opacity: 0;
}
.after_select {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.page {
  width: 100%;
  max-width: 500px;
}

@media (max-width: 1440px) {
  .tel {
    height: 40px;
    font-size: 14px;
  }
  .phoneinput_select {
    width: 50px;
    height: 30px;
  }
}

@media (max-width: 1200px) {
  .tel {
    height: 35px;
    font-size: 16px;
    width: 100%;
    font-size: 14px;
  }
  .phoneinput_select {
    width: 40px;
    height: 20px;
  }
  .default_value {
    margin: 0 10px;
    font-size: 14px;
  }
}
    
@media (max-width: 800px) {
  .page {
    width: 80%;
    margin: 0 0 10px 0;
  }
  .tel {
    height: 50px;
  }
}

@media (max-width: 500px) {
  .page {
    width: 100%;
    margin: 0 0 10px 0;
  }
  .tel {
    height: 50px;
  }
}